.Input_General {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.Input {
    width: 100%;
    display: block;
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 10px;
    box-sizing: border-box;
}

.InputElement:focus {
    outline: none;
    background-color: #ccc;
}

.Invalid {
    border: 1px solid red;
    background-color: #fda49a;
}
